/**
 * API
 */
import { createInstance, getBaseURL, parseError, provideInterceptors, throwError } from '@/utils/http'
import { get } from 'lodash'
let instance = null

export function getInstance(){
    if (!instance){
        instance =  provideInterceptors(
            createInstance({
                baseURL: getBaseURL() + '/v1/api/project/export',
                crossDomain: false,
                withCredentials: true,
                headers: {
                    'Content-Type': 'application/json'
                },
                timeout: 1000 * 15
            }),
            { request: true }
        )
    }
    return instance
}


let instance2 = null

export function getInstance2(){
    if (!instance2){
        instance2 =  provideInterceptors(
            createInstance({
                baseURL: getBaseURL() + '/v1/api/project/export',
                crossDomain: false,
                withCredentials: true,
                headers: {
                    'Content-Type': 'application/json'
                },
                timeout: 1000 * 60
            }),
            { request: true }
        )
    }
    return instance2
}

/**
 *
 * @param processId ID
 * @param exportType  1 or  2
 * @param totalNum 
 * @param projectId  ID
 * @returns {Promise<void>}  --> export_id
 */
export async function preExport(processId, exportType, totalNum, projectId) {
    try {
        let params = {
            process_id: processId,
            export_type: exportType,
            total_num: totalNum,
            project_id: projectId
        }
        const response = await getInstance().post('/pre', params)
        const res = get(response, 'data.data', {})
        return res
    } catch (err) {
        throwError(err, code => {
            console.log(code)
            switch (code) {
                case 400:
                    return ''
                default: {
                }
            }
        })
    }
}

/**
 *  
 * @param projectId
 * @param exportIds ID [{"export_id":1}, {"export_id":2}]
 * @param originId ID
 * @param effectParam 
 * @param imageType   1   2
 * @param imageFormat  jpeg  1 png  2
 * @param order 
 * @returns {Promise<void>}
 */
export async function uploadEffectImage(projectId, exportIds, originId, effectParam, imageType, imageFormat, order, file) {

    try {
        const formData = new FormData()
        formData.append('file', file)
        const response = await getInstance2().post('/result', formData, {
            headers: {
                'Project-Id': projectId,
                'Export-Ids': exportIds,
                'Origin-Id': originId,
                'Effect-Param': effectParam,
                'Image-Type': imageType,
                'Image-Format': imageFormat,
                'Order': order,
                'Content-Type': 'multipart/form-data'
            }
        })
        const res = get(response, 'data.data', {})
        return res
    } catch (err) {
        throwError(err, code => {
            console.log(code)
            switch (code) {
                case 400:
                    return ''
                default: {
                }
            }
        })
    }

}


