/**
 * API
 */
import { throwError, provideInterceptors, createInstance, getBaseURL } from '@/utils/http'
import { get } from 'lodash'

let instance = null

export function getInstance(){
    if (!instance){
        instance =  provideInterceptors(
            createInstance({
                baseURL: getBaseURL() + '/v1/api/project/image',
                crossDomain: false,
                withCredentials: true,
                headers: {
                    'Content-Type': 'application/json'
                },
                timeout: 1000 * 60
            }),
            { request: true }
        )
    }
    return instance
}


/**
 * ID
 * @param projectId
 * @returns {Promise<any>}
 */
export async function postCreateThumbnailId(projectId, batch_num) {
    try {
        let params = {
            project_id: projectId,
            batch_num: batch_num
        }
        const response = await getInstance().post('/batch_id', params)
        const res = get(response, 'data.data', {})

        return res
    } catch (err) {
        throwError(err, code => {
            console.log(code)

            switch (code) {
                case 400:
                    return ''
                case 500:
                    return ''
                default: {
                }
            }
        })
    }
}

/**
 * 
 * @param projectId
 * @param imageId
 * @returns {Promise<any>}
 */
export async function deleteThumbnailIds(projectId, image_ids) {
    try {
        let params = {
            project_id: projectId,
            image_ids: image_ids
        }

        const response = await getInstance().delete('/batch_id', {
            data: params
        })
        const res = get(response, 'data.data', {})
        return res
    } catch (err) {
        throwError(err, code => {
            console.log(code)
            switch (code) {
                case 400:
                    return ''
                default: {
                }
            }
        })
    }
}


/**
 * 
 * @param projectId  ID
 * @param imageId  ID
 * @param imageFormat  1 JPEG 2 PNG 3JPG etc
 * @param imageData  
 * @returns {Promise<void>}
 */
export async function postUploadOriginImage(projectId, imageId, imageFormat, imageData) {
    try {
        const response = await getInstance().post('/info', null, {
            headers: {
                'Project-Id': projectId,
                'Image-Id': imageId,
                'Image-Format': imageFormat
            }
        })
        const res = get(response, 'data.data', {})
        return res
    } catch (err) {
        throwError(err, code => {
            console.log(code)
            switch (code) {
                case 400:
                    return ''
                default: {
                }
            }
        })
    }

}

