/**
 * 
 * @param type  1 2 ext...
 * @param isSystem , false 
 * @returns {Promise<void>}
 */
export async function getSetting(type, isSystem) {
    try {
        let params = {
            type: type,
            is_system: isSystem
        }
        const response = await getInstance().get('/get', { params })
        const res = get(response, 'data.data', {})
        return res
    } catch (err) {
        throwError(err, code => {
            console.log(code)
            switch (code) {
                case 400:
                    return ''
                default: {
                }
            }
        })
    }
}

